export const primsalgo=(grid,startnode,finishnode)=>{
  let addedWalls = [];
  let removedWalls = [];
  
  let rows= grid.length
  let columns= grid[0].length

  let open = {};
  let frontier = {};

  for (let i = 0; i < rows; i++) {
    for (let j = 0; j < columns; j++) {
      if (i % 2 === 0 || j % 2 === 0) {

      } 
      else open[getKey(i, j)] = grid[i][j];

      if(grid[i][j].start || grid[i][j].end) continue
      //for each node add walls
      grid[i][j].isWall = true;
      addedWalls.push(grid[i][j]);
    }
  }

  //boundary
  const f = [
    [2, 0],
    [-2, 0],
    [0, 2],
    [0, -2]
  ];

  //neighbours
  const n = [
    [1, 0],
    [-1, 0],
    [0, 1],
    [0, -1]
  ];

  //pick a random value from open and consider it as start node 
  const start = open[randomKey(open)];
  //remove the wall
  grid[start.row][start.column].isWall = false;
  //push to removed wall
  removedWalls.push(grid[start.row][start.column]);

  f.forEach(i => {
    const r = start.row + i[0]; //startrow + 2, statrow-2, startrow+0, startrow+0
    const c = start.column + i[1]; //startcol + 0, startcol + 0 ,startcol +2 ,startcol-2

    if (
      grid[r] && //if that row exsist
      grid[r][c] && //if that row and col exsist
      grid[r][c].isWall && //if it is a wall
      r !== 0 && //it is not the 0 th row
      c !== 0 && //it is not the 0 th col
      r !== rows - 1 && //it is not the end row 
      c !== columns - 1 //it is not the end column
    )
    //then push it to frontier
      frontier[getKey(r, c)] = grid[r][c];
  });
  // console.log(frontier)

  while (Object.keys(frontier).length) //continue till border(frontier) not empty
   {
    const randFKey = randomKey(frontier); //get a random key

    const { row, column } = frontier[randFKey]; //get that element row and col

    let neighbours = {}

    f.every((i, index) => {
      //boundary
      const r = row + i[0]; //row+2,row-2,row+0,row-0
      const c = column + i[1];//col+0,col-0,col+2,col-0
      //get neighbours
      const wr = row + n[index][0];
      const wc = column + n[index][1];
      //if neighbours exsist and it is wall then add that to neighbours
      if (grid[r] && grid[r][c] && !grid[r][c].isWall) {
        neighbours[getKey(wr, wc)] = grid[wr][wc];
      }
      //if boundary is not preset then return 
      return true;
    });
    
    //get a random neighbour
    const randNeighbour = neighbours[randomKey(neighbours)];
    //remove the wall
    grid[randNeighbour.row][randNeighbour.column].isWall = false;
    grid[frontier[randFKey].row][frontier[randFKey].column].isWall = false; 
    removedWalls.push(grid[randNeighbour.row][randNeighbour.column]); //remove wall of rnadom neighbour
    removedWalls.push(grid[frontier[randFKey].row][frontier[randFKey].column]);//remove the walls for that key in fromtier


    //for that particticular rnadkey find the boundary and push it to frontier
    f.forEach(i => {
      const r = frontier[randFKey].row + i[0];
      const c = frontier[randFKey].column + i[1];
      if (
        grid[r] && //if that row exsist
        grid[r][c] &&  //if that row and col exsist
        grid[r][c].isWall && //if it is a wall
        r !== 0 && //it is not the 0 th row
        c !== 0 && //it is not the 0 th col
        r !== rows - 1 && //it is not the end row
        c !== columns - 1 //it is not the end column
      )
        frontier[getKey(r, c)] = grid[r][c]; //push to frontier
    });
    delete frontier[randFKey]; //delete the rnadom key value from frontier
  }
  return { addedWalls, removedWalls};
};



//generate a random key
const randomKey = obj => {
  var keys = Object.keys(obj);
  return keys[(keys.length * Math.random()) << 0];
};

//convert row and col to key "row-col"
const getKey = (i, j) => {
  return i.toString() + "-" + j.toString();
};


